import Image from "next/image";
import { Badge } from "@/components/ui/badge";
import { Github, ExternalLink } from "lucide-react";
import Link from "next/link";

interface ProjectProps {
  title: string;
  description: string;
  technologies: string[];
  imageUrl: string;
  githubUrl: string;
  liveUrl?: string;
}

function ProjectCard({ title, description, technologies, imageUrl, githubUrl, liveUrl }: ProjectProps) {
  return (
    <div className="mb-16 flex flex-col md:flex-row gap-8 items-start">
      <div className="w-full md:w-1/3 rounded-md overflow-hidden">
        <div className="relative w-full aspect-[4/3]">
          <Image
            src={imageUrl}
            alt={title}
            fill
            className="object-cover"
            crossOrigin="anonymous"
          />
        </div>
      </div>
      <div className="flex-1">
        <h3 className="text-xl font-medium mb-3">{title}</h3>
        <p className="text-muted-foreground mb-4 max-w-3xl">{description}</p>
        <div className="flex flex-wrap gap-2 mb-4">
          {technologies.map((tech) => (
            <Badge key={tech} variant="secondary">{tech}</Badge>
          ))}
        </div>
        <div className="flex gap-4">
          <Link
            href={githubUrl}
            target="_blank"
            rel="noopener noreferrer"
            className="inline-flex items-center gap-2 text-sm hover:text-primary transition-colors"
          >
            <Github size={16} />
            <span>GitHub</span>
          </Link>
          {liveUrl && (
            <Link
              href={liveUrl}
              target="_blank"
              rel="noopener noreferrer"
              className="inline-flex items-center gap-2 text-sm hover:text-primary transition-colors"
            >
              <ExternalLink size={16} />
              <span>Live Demo</span>
            </Link>
          )}
        </div>
      </div>
    </div>
  );
}

